<template>
  <div class="round-bar flex">
      <div class="selector-box">
            <coin-selector/>
      </div>
    

    <ul class="resolutions flex" v-show="false">
      <li v-for="res in resolutions" class="bar animate"
          :class="{'shadow-active': resolution===res.type}"
          :key="res.type" @click="res.online&&changeResolution(res.type)">
        <resolution-counter class="res-counter" :item="res"/>
      </li>
    </ul>
    
  </div>
</template>

<script>
import {mapActions, mapState} from "vuex";
import ResolutionCounter from "@/views/optionComps/resolutionCounter";
import CoinSelector from "@/views/common/coinSelector";

export default {
  name: "roundBar",
  components: {CoinSelector, ResolutionCounter},
  data() {
    return {
      resolutions: [
        {text: 'resolution_5', type: '5', online: true},
        {text: 'resolution_15', type: '15', online: true},
      ],
      blockTimer: null,
    }
  },
  computed: {
    ...mapState('option',['symbol', 'resolution']),
  },
  methods: {
    ...mapActions('option',['setCurrentBlock']),
    getCurrentBlock() {
      clearInterval(this.blockTimer);
      this.blockTimer = setInterval(() => {
        if (!['prediction', 'index'].includes(this.$route.name)) return
        this.$api.getBlock('pending').then(({number}) => {
          this.setCurrentBlock(number);
        })
      }, 1000);
    },
    changeResolution(resolution) {
      let {symbol} = this;
      this.$router.push({path: '/prediction', query: {symbol, resolution}})
    }
  },
  beforeMount() {
    this.getCurrentBlock();
  },
   beforeUnmount(){
      clearInterval(this.timer);// 清除定时器
      this.timer = null;
   },
}
</script>

