<template>
  <div class="head-bar flex jc-between">
    <div class="flex flex-wrap ai-center">
      <img class="menu-icon icon" @click="setMenuHidden(!menuHidden)" :src="menuIcons[menuHidden?0:1]" alt="icon"/>
      <router-link class="logo flex ai-center" to="/index">
        <img src="../../images/option/logo.png" alt="LOGO"/>
      </router-link>
     
      <!--<span class="mobile-tt text flex ai-center fs-20">{{language[title]}}</span> -->

      <!-- 美式期权 -->
      <div class="bar-group">
        <round-bar v-if="checkMenuPath('/prediction')"/>
      
         <div v-if="routerStatus">
               <m-tabs class="sub-tab" v-if="currentTabs"
                :gap="20" :on-tab-change="tabChange"
                :tabs="currentTabs" :tab-index="tabIndex"/>
         </div>
      </div>

        <!-- 添加流动性 -->
      <div class="tab-bar flex ai-center"  v-if="$route.path === '/option/revenueRecord'" >
         <m-tabs :tabs="tabs" v-model:tab-index="getHistoryTable"
              :enable="!loading"
              :on-tab-change="tabChangeHandle"/>
      </div>
    
        <!-- 期权记录 -->
       <div class="tab-bar flex ai-center"  v-if="$route.path === '/option/transactionList'" >
         <m-tabs :tabs="tabsList" v-model:tab-index="getOptionList"
              :enable="!loading"
              :on-tab-change="tabChangeHandleListFn"/>
        </div>

      <!-- 农场 -->
      <div class="tab-bar flex ai-center" v-if="  
          $route.path === '/pledgeFarm/farm' ||
          $route.path ===  '/pledgeFarm/NftMining'||
          $route.path ===  '/pledgeFarm/optionAmerican'"
       >
    
         <m-tabs :tabs="tableFarm" v-model:tab-index="tableId"
              :enable="!loading"
              :on-tab-change="tabChangeFarm"/>
      </div>

       <!-- 盲盒 -->
      <div class="tab-bar flex ai-center"  v-if="
             $route.path == '/tradeFair/blindBox'||
             $route.path == '/tradeFair/exchange'||
             $route.path == '/tradeFair/market'||
             $route.path == '/tradeFair/marketOrderRecordList' ||
             $route.path ==  '/tradeFair/detailsNftCard'
      " >
    
        <m-tabs :tabs="tableTradeFair" v-model:tab-index="tableIndex "
              :enable="!loading"
              :on-tab-change="tradeFair"/>
      </div>


    </div>
    <div class="right-bar flex">
      <record-bar v-if="!isHome&&address"/>
      <author-bar/>
    </div>
  </div>
</template>

<script>
import AuthorBar from "@/views/common/authorBar";
import RoundBar from "@/views/common/roundBar";
import {mapActions, mapGetters, mapState} from "vuex";
import icon_menu from '@/images/menu.png';
import icon_menu1 from '@/images/menu_1.png';
import MTabs from "@/components/mTabs";
import RecordBar from "@/views/common/recordBar";
export default {
  name: 'headBar',
  components: {RecordBar, MTabs, RoundBar, AuthorBar},
  computed:{
    ...mapGetters(['language']),
    ...mapState('app',['title','menuHidden','address','getHistoryTable','getOptionList','getTableTrade']),
    currentTabs(){
      let name = this.$route.matched[0]?.name;
      return this.allTabs[name]
    },
    tabIndex(){
      return this.currentTabs.findIndex(t=>this.checkMenuPath(t.path))
    },
    isHome() {
      return this.$route.name === 'index'
    },
   
  },
  data() {
    return {
      tableId:0,
      tabIndexs:0,
      tabIndexHistory:0,
      tableIndex:0,
      // 盲盒
      tableTradeFair:[
          {type: 0, language_key:'blindBox'},
          {type: 1, language_key:'market'},
          // {type: 2, language_key:'exchange'}
        ],
      tableFarm: [
        {type: 0, language_key:'menu_3'},
        {type: 1, language_key: "NFT"},
        {type: 2, language_key:'option_tt'},
      ],

      tabsList: [
        {type: 0, language_key: 'aoption_holding_trade'},
        {type: 1, language_key: 'aoption_history_trade'},
      ],
      tabs: [
        {type: 1, language_key: 'aoption_holding_lp'},
        {type: 2, language_key: 'aoption_history_lp'},
      ],
      routerStatus:true,
      menuIcons:[icon_menu,icon_menu1],
      allTabs: {
        nft:[
           {type: 0,path:'/nft/bag', language_key: 'my_nft'},
           {type: 1,path:'/nft/lottery', language_key: 'tt_4_1'},
          {type: 2,path:'/nft/fragmentSynthesis', language_key: 'tt_4_synthesis'},
        ],
        option:[
          // {type: 0,path:'/option/index', language_key: 'aoption_new_option'},
          // {type: 1,path:'/option/liquidity', language_key: 'aoption_liquidity'},
        ],
        vip:[
          {type: 0,path:'/vip/member', language_key: 'label_5_12'},
          {type: 1,path:'/vip/invitation', language_key: 'invitation'},
        ],
      },
    };
  },
  created(){
    //  let tab = localStorage.getItem("tableIndex")?localStorage.getItem("tableIndex"):0
    //  this.tableIndex = parseInt (tab)
   


     let tabs = localStorage.getItem("tableIndexFarm")?localStorage.getItem("tableIndexFarm"):0
     this.tableId = parseInt (tabs)
 },

  methods: {
    ...mapActions('app',[
      'setMenuHidden',
      'setHistoryTable',
      'setOptionList',
      'setTableTrade',
      'setPledgeFarm'
      ]),
    // 盲盒
    tradeFair(index){
       if(index == 1){
           this.$router.push({ path: '/tradeFair/market'}); //市场
       }else if(index == 0){
           this.$router.push({ path: '/tradeFair/blindBox'}); //盲盒
       }
      //  else {
      //     //  this.$router.push({ path: '/tradeFair/exchange'}); //兑换
      //  }
       this.tableIndex = parseInt(index) 
       localStorage.setItem("tableIndex",index);//存储
    },

    tabChangeHandle(index){
       this.setHistoryTable(index)
       this.$router.push({ path: '/option/revenueRecord'});
    },
    tabChangeHandleListFn(index){
        this.setOptionList(index)
        this.$router.push({ path: '/option/transactionList'});
    },
    // 农场
    tabChangeFarm(index){
       this.tableIndex = parseInt(index) 
       localStorage.setItem("tableIndexFarm",index);//存储
      if(index == "0"){
           this.$router.push({ path: '/pledgeFarm/farm'})    
      }else if(index == 1){
           this.$router.push({ path: '/pledgeFarm/NftMining'})    
      }else{
            this.$router.push({ path: '/pledgeFarm/optionAmerican'})    
      }
    },
    checkMenuPath(path){
      return this.$route.path.indexOf(path)!==-1
    },
    tabChange(idx,t){
      this.$router.push(t.path);
    }
  },
  watch:{
    $route(to){
      if(to.path =="/option/index" ||  to.path =="/option/liquidity"){
            this.routerStatus = false;
      }else{
            this.routerStatus = true;
      }
        
      if(to.path == "/tradeFair/optionAmerican"){
            this.tableId = 2;
      }else if(to.path == "/pledgeFarm/NftMining"){
            this.tableId = 1;
      }else if(to.path == "/pledgeFarm/farm"){
            this.tableId = 0;
      } 
      if(to.path == "/tradeFair/blindBox"){
            this.tableIndex = 0
      }else if(to.path == "/tradeFair/market" || to.path == "/tradeFair/marketOrderRecordList" ){
            this.tableIndex = 1
      }else{
            this.tableIndex = 1
      }

   },
  
  }
};
</script>

<style lang="less" scoped>
.head-bar {
  position: fixed;
  width: 100%;
  height: 60px;
  top: 0;
  left: 0;
  background-color: #191F2D;
  z-index: 9;
}
.menu-icon{
  // margin: 13px 0 0 15px;
  cursor: pointer;
  margin: 0 15px;
}
