  height: 60px;
  background-color: #191F2D;
  img {
    height: 50px;
    display: block;
  }
}
.mobile-tt{
  margin-left: 10px;
  display: none;
  margin-right: 20px;
}
.bar-group{
  background-color: #191F2D;
  padding-left: 80px;
}
.sub-tab{
  line-height: 60px;
}
.right-bar{
  padding-right: 60px;
  position: relative;
  top: 0;
  right: 0;
  z-index:999;
  // background: red;
  cursor: pointer;
}
@media screen and (max-width: 1024px) {
  .logo{
    display: none;
  }
  .mobile-tt{
    display: flex;
  }
  .right-bar{
    padding-right: 10px;
  }
  .bar-group{
    padding-left: 0;
  }
  .menu-icon{
    width: 25px;
    // margin: 0 8px;
    height: 25px;
  }
}
</style>