import {createStore} from 'vuex'
import app from '@/store/module/app';
import option from '@/store/module/option';
import nft from '@/store/module/nft';
import farm from '@/store/module/farm';
import vip from '@/store/module/vip';
import aoption from '@/store/module/aoption';
import {chainConfig} from "@/common/walletConfig";
import {getLanguage} from "@/common/util";
export default createStore({
  getters:{
    coin: (state, getters) => getters.currentChain.nativeCurrency.symbol,
    currentRound: ({option}) => option.currentRounds[option.resolution],
    currentEpoch: (state, getters) => getters.currentRound.epoch,
    currentChain: state => chainConfig[state.app.chainId],
    language: state => getLanguage(state.app.locale),
    t: (state) => {
      // getLanguage()
      return (key, data) => {
        const translationSet = getLanguage(state.app.locale)
        const translatedText = (translationSet && translationSet[key]) || key || '';
        const includesVariable = translatedText.match(/%\S+?%/gm);

        if (includesVariable && data) {
          let interpolatedText = translatedText;
          Object.keys(data).forEach(dataKey => {
            const templateKey = new RegExp(`%${dataKey}%`, 'g');
            interpolatedText = interpolatedText.replace(
              templateKey,
              data[dataKey].toString()
            );
          });

          return interpolatedText;
        }

        return translatedText;
      }
    },
  },
  modules: {
    app,
    option,
    nft,
    farm,
    vip,
    aoption
  }
})
