                />
                +{{ item.gain || 0 }}
              </b>
              <!-- <b class="text flex ai-center" v-else>
                <img class="icon-s" src="../../images/option/nft-copy.png" alt=""/>
                  {{language.nft_fragment}}
              </b> -->
            </span>
            <img
