import {BN, checkDevice} from "@/common/util";

let device = checkDevice();
let menuHidden = sessionStorage.getItem('menu_hidden');
menuHidden = device === 'mobile' ? true : menuHidden === 'true';
export default {
  namespaced: true,
  state: {
    title: '',
    device,
    menuHidden,
    chainId: '0x61',
    address: '',
    getHistoryTable:0,
    getOptionList:0,
    getTableTrade:0,
    balance: {
      BNB: new BN(0),
      USDT: new BN(0),
      DFT: new BN(0),
      DFV: new BN(0),
      ETH: new BN(0),
      CAKE: new BN(0),
      WBNB: new BN(0),
      DCoin: new BN(0),
    },
    locale: localStorage.getItem('locale') || 'en',
  },
  mutations: {
    setDevice(state, device) {
      state.device = device;
    },
    setTitle(state, title) {
      state.title = title;
    },
    setMenuHidden(state, bool) {
      state.menuHidden = bool;
      sessionStorage.setItem('menu_hidden', bool);
    },
    setLocale(state, locale) {
      state.locale = locale;
      localStorage.setItem('locale', locale);
    },
    setUserInfo(state, {address, BNB, DFT, DFV, USDT, ETH, CAKE, WBNB,DCoin}) {
      if (address !== undefined) {
        // state.address = '0xE3d9023a29C680F83E039056DA2948B65330eB4c'.toLowerCase();
        state.address = address.toLowerCase();
      }
      if (BNB !== undefined) {
        state.balance.BNB = BNB instanceof BN ? BNB : new BN(BNB);
      }
      if (USDT !== undefined) {
        state.balance.USDT = USDT instanceof BN ? USDT : new BN(USDT);
      }
      if (DFT !== undefined) {
        state.balance.DFT = DFT instanceof BN ? DFT : new BN(DFT);
      }
      if (DFV !== undefined) {
        state.balance.DFV = DFV instanceof BN ? DFV : new BN(DFV);
      }
      if (ETH !== undefined) {
        state.balance.ETH = ETH instanceof BN ? ETH : new BN(ETH);
      }
      if (CAKE !== undefined) {
        state.balance.CAKE = CAKE instanceof BN ? CAKE : new BN(CAKE);
      }
      if (WBNB !== undefined) {
        state.balance.WBNB = WBNB instanceof BN ? WBNB : new BN(WBNB);
      }
      if (DCoin !== undefined) {
        state.balance.DCoin = DCoin instanceof BN ? DCoin : new BN(DCoin);
      }
    },
    setCurrentBlock(state, block) {
      state.currentBlock = block;
    },
    //  添加流动性 table
    setHistoryTable(state, status){
      state.getHistoryTable = status;
    },
    // 期权记录table
    setOptionList(state, status){
      state.getOptionList = status;
    },
    // 盲盒
    setTableTrade(state, status){
      state.getTableTrade = status;
    },
  },
  actions: {
    setDevice({commit}, device) {
      commit('setDevice', device);
    },
    setTitle({commit}, title) {
      commit('setTitle', title);
    },
    setMenuHidden({commit}, bool) {
      commit('setMenuHidden', bool);
    },
    setLocale({commit}, locale) {
      commit('setLocale', locale);
    },
    setUserInfo({commit}, params) {
      commit('setUserInfo', params);
    },
    setCurrentBlock({commit}, block) {
      commit('setCurrentBlock', block);
    },
    // 添加流动性table
    setHistoryTable({commit}, block){
      commit('setHistoryTable', block);
    },
    // 期权记录table
    setOptionList({commit}, status){
      commit('setOptionList', status);
    },
     // 盲盒
     setTableTrade({commit}, status){
      commit('setTableTrade', status);
    }
  }
}