<template>
  <div class="dialog" v-show="dailyLoginStatus">
    <div class="dialog-container">
      <div class="dialog-title">{{language.signIndTata}}</div>
      <div class="reward">{{language.getFree}}</div>

      <div class="close">
        <img src="../../../images/close@2x.png" alt="" @click="closeFn()" />
      </div>

      <div class="signIn">
        <div v-for="(item, index) in list" :key="index" :class="getDay == index ? 'signList1' : ''" class="signList">
          <span v-if="getDay == index && item == 0" class="sign" @click="signInFn"> {{language.signIn}} </span>
          <span v-else-if="getDay == index && item != 0" class="amoun">
                {{index}} 
                <img src="../../../images/confirm.png" alt="">
          </span>
          <span v-else>{{index}} </span>
        </div>
      </div>
        <div class="promptText" >
           {{language.rewardContinuity}}  <span>80 DCoin</span>
         
        </div>
      <div class="congratulations">
         <span v-if="bonusAmount  ">{{language.signGet}} ({{bonusAmount}}) DCoin</span>
        <!-- <div v-if="true">{{language.rewardContinuity}}</div>
        <div v-else>{{language.congratulations}} !</div> -->
        
      </div>
    </div>
  </div>
</template>

<script>
import {mapGetters} from "vuex";
import {BN} from "@/common/util";
export default {
  props: {
    dailyLoginStatus: {
      type: Boolean,
      default: false,
    },
  },
  watch: {
    dailyLoginStatus() {
      this.getCheckinsFn();
    },
  },
   computed: {
     ...mapGetters(['language']),
  },
  data() {
    return {
      getDay:"",
      list: [],
      signStatus: [8, 2, 20, 15, 4],
      bonusAmount:"",
    };
  },
  mounted() {
    this.getDateFn();
    setTimeout(()=>{
         this.checkins();
    },1000)
    
  },
  methods: {
    closeFn() {
      this.$emit("closeDailyFn");
    },

    // 显示金额
      checkins(){
          let day =  parseInt(new Date().getTime()/1000/3600/24);
          this.$api.checkins(day).then((res) =>{
            this. bonusAmount = parseInt(new BN(res.bonusAmount).toDisplay('balance').myFixed())
        })
      },
      
    // 确定签到
    signInFn(){
      this.$api.checkin().then(() =>{
          this.getCheckinsFn();
          this.checkins();
      })
    },
    getCheckinsFn() {
      var now = new Date(); // 当前日期
      var nowMonth = now.getMonth(); //当前月 （值为0~11）注意此处范围
      var nowYear = now.getFullYear(); //当前年
      var firstDay = parseInt(
        new Date(nowYear, nowMonth, 1).getTime() / 1000 / 3600 / 24
      );
      this.$api.getCheckins(firstDay, this.mGetDate()).then((res) => {
        this.list = res;
      });
    },
    // 获取这个月的天数
    mGetDate() {
      var date = new Date();
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var d = new Date(year, month, 0);
      return d.getDate();
    },
    //  获取当前是多少号
    getDateFn(){
      var date = new Date();
      this.getDay  =  date.getDate();
    }
     
  },
};
</script>
