<template>
  <div class="option-record">
    <div class="tab-bar flex ai-center">
      <m-tabs :tabs="tabs" v-model:tab-index="tabIndex"
              :enable="!loading"
              :on-tab-change="tabChangeHandle"/>
    </div>
    <div class="list-box">
      <ul class="m-folder-list">
        <li v-for="item in transactionList" :key="item.id">
          <div class="main-info flex jc-between ai-center" @click="openFlag[item.id] = !openFlag[item.id]">
            <!-- <b class="win-text" :class="item.isWinner?'green':'red'">
              {{ item.isWinner ? language.win : language.lose }}
            </b> -->
            <img  class="win-text" :src="item.direction === 0 ? downImage : upImage" alt="">
            <span class="c1">{{ language.type }}<br><b class="text">{{ item.symbol }}</b></span>
            <span class="c2">P&L<br><b class="text">{{ item.PL }}</b></span>
            <span class="c2">
              {{language.aoption_time_left}}<br>
              <b class="text text-nowrap">
                {{ item.displayCountdown }}
              </b>
            </span>
            <img v-if="openFlag[item.id]" class="icon arrow" src="@/images/option/order1.png" alt=""/>
            <img v-else class="icon arrow" src="@/images/option/order.png" alt=""/>
          </div>
          <div class="detail-info animate-inout" :class="{open:openFlag[item.id]}">
            <p class="base-info flex jc-between">
              <span class="s1">
               {{language.aoption_lp_amount}}<br>
                <b class="text fs-20">{{ item.amount }} {{ item.symbol }}</b>
              </span>
              <span class="s3 ta-right">
                {{language.aoption_trade_amount}}<br>$
                <span class="text">{{ item.optionPrice }}</span>
              </span>
            </p>
            <div v-show="true" class="res-box flex jc-between ai-center border-gray hdr-el-gray"
              :class="{'border-green hdr-el-green': true,'border-red hdr-el-red': false}"
            >
              <span class="s1">
                {{language.aoption_trade_price}}($)<br>
                <b class="fs-20 text" >{{item.displayStrike}}</b>
              </span>
              <span class="flex1">
                {{language.aoption_draw_price}}($)<br>
                <span class="flex ai-center text">
                  <!-- coin, direction, amount, optionPrice -->
                  {{ displayEvenPrive(item.strike, item.direction, item.amount, item.optionPrice) }}
                  <img v-if="true" class="icon" src="../../../images/option/up.png" alt=""/>
                  <img v-if="false" class="icon" src="../../../images/option/down.png" alt=""/>
                </span>
              </span>
              <span class="s3 ta-right">
                {{language.aoption_currency_price}}($)<br>
                <span class="text">{{ coinPrices[item.symbol].price.toDisplay(coinPrices[item.symbol].decimals).myFixed() }}</span>
              </span>
            </div>
            <footer class="flex jc-between ai-center">
                <p class="flex1">
                  <span class="res-text d-ib text">{{ language.aoption_pre_profit }}USDT<br>
                  <b class="gain d-ib text fs-20">{{
                    displayProfit(item.symbol, item.direction, item.strike, item.amount, item.optionPrice)
                  }}</b>
                  </span>
                </p>
              <p class="block flex jc-between ta-center">
                <span></span>
                <span @click="openBlock(item.createHash)">
                  {{language.aoption_block}}<br>
                  <!-- {{ language.end_block }}<br> -->
                  <img class="icon-l" src="@/images/option/block.png" alt=""/>
                </span>
              </p>
              <m-button
                class="btn-claim hdr-el-blue"
                :ref="'btn-'+item.id"
                v-if="item.isEnd"
                @click="exerciseOptionFacade(item.id, $refs['btn-'+item.id])"
              >
                {{ language.aoption_wind_up }}
              </m-button>
              <m-button
                class="btn-claim hdr-el-gray border-gray gray"
                disabled
                v-else
              >
                {{ language.aoption_wind_up }}
              </m-button>
            </footer>
          </div>
        </li>
        <li class="no-data flex jc-center ai-center" v-show="!loading&&transactionList.length===0">
          <p class="text fs-16 ta-center">
            <img src="@/images/no-data.png" alt="nodata"><br>{{ language.no_data }}
          </p>
        </li>
      </ul>
      <div v-if="loading" class="mask flex jc-center ai-center ta-center">
        <span><i class="icon-loading"></i><br>{{ language.tip_loading }}...</span>
      </div>
    </div>
  </div>
</template>

<script>
import MTabs from "@/components/mTabs";
import {mapGetters, mapState} from 'vuex';
import {blockConfig} from "@/common/walletConfig";
import MButton from "@/components/mButton";
import { encodeDefxCallName, getDefxCallType, BN } from "@/common/util";
import upImage from '@/images/commons/up.png'
import downImage from '@/images/commons/down.png'
import {coinsTabs} from '../datas'

export default {
  name: "optionRecord",
  components: {MButton, MTabs},
  computed: {
    ...mapGetters(['language']),
    ...mapState('app', ['address', 'device']),
    ...mapState('option', ['currentRounds', 'contractName']),
    ...mapState('aoption', ['options']),
  },
  watch: {
    currentRounds() {
      this.getList();
    },
  },
  data() {
    return {
      upImage,
      downImage,
      tabIndex: 0,
      tabs: [
        {type: 0, language_key: 'aoption_holding_trade'},
        {type: 1, language_key: 'aoption_history_trade'},
      ],
      transactionList: [],
      loading: false, //加载中
      openFlag: {},
      roundMap: {
        '5': '5M',
        '15': '15M'
      },
      coinPrices: (() => {
        const resval = {}
        coinsTabs.forEach(item => {
          resval[item.symbol] = {
            price: new BN(0),
            decimals: 8,
          }
        })
        return resval
      })(),
    }
  },
  methods: {
    getCoinPrice(priceContractName, coin) {
      this.$api.getLatestPrice(priceContractName).then(res=>{
        this.coinPrices[coin].price = new BN(res.answer)
        this.coinPrices[coin].decimals = Number(res.decimals)
        console.log(this.coinPrices)
      })
    },
    displayEvenPrive(strike, direction, amount, optionPrice) {
      // 看涨： 打平价格 = 行权价格 +（权利金 /  买入数量）
      // 看跌： 打平价格 = 行权价格 - （权利金 /  买入数量）
      // const value = new BN(amount).toWei('balance').toString(10)
      console.log(direction)
      if (direction === 1) {
        return new BN(strike).toDisplay().plus(new BN(optionPrice).div(new BN(amount).toWei('balance').toString(10))).myFixed()
      }
      return new BN(strike).toDisplay().minus(new BN(optionPrice).div(new BN(amount).toWei('balance').toString(10))).myFixed()
    },
    displayProfit(coin, direction, strike, amount, optionPrice) {
      // 看涨收益: (当前价格-行权价格)* 数量 - 期权成本
      // 看跌收益: (行权价格 - 当前价格)* 数量 - 期权成本

      const coinPrice = this.coinPrices[coin].price
      if (direction === 1) {
        const upPrice = coinPrice.minus(strike).times(amount).toDisplay()
        return new BN(upPrice).minus(optionPrice).myFixed()
      }
      const downPrice = new BN(strike).minus(coinPrice).times(amount).toDisplay()
      return new BN(downPrice).minus(optionPrice).myFixed()
      // return this.price.minus(this.dispalyPrice).times(this.amount).minus(this.optionPrice)
    },
    getCountdownTime(hedged, timestamp, direction, symbol) {
      let endTime = 0
      const contractName = encodeDefxCallName(getDefxCallType(direction), symbol)
      console.log(this.options[contractName], this.options, contractName)
      // 对冲结束时间
      if (hedged) {
        endTime = Number(timestamp)  + Number(this.options[contractName].lockupPeriodForHedgedTranches)
      } else { // 非对冲结束时间
        endTime = Number(timestamp)  + Number(this.options[contractName].lockupPeriodForUnhedgedTranches)
      }
      const nowTime = new Date().getTime()
      const countdown = endTime * 1000 - nowTime
      return countdown
    },
    getList() {
      if (this.loading) return;
      this.loading = true;
      this.$api.getFacadeHistoryList(this.address, this.tabIndex+1).then(({ options }) => {
        console.log(options)
        this.loading = false;
        const nowTime = new Date().getTime()
        this.transactionList = options.map(item => {
          const countdownTime = Number(item.expiredtime) * 1000 - nowTime
          const isEnd = !(countdownTime > 0)
          const displayCountdown = isEnd ? '00:00' : new Date(countdownTime).countdown('dd{day} hh:mm').replace('{day}', this.language.d)
          const displayTimestamp = new Date(Number(item.timestamp) * 1000).format('yyyy-MM-dd') 
          const drawPrice = '0'
          return {
            ...item,
            displayCountdown,
            isEnd,
            displayTimestamp,
            drawPrice,
            displayStrike: new BN(item.strike).toDisplay().myFixed(),
            PL: 0, // new BN(item.strike).minus(item.total).toDisplay().myFixed()
          }
        });
      }).catch((error) => {
        console.error(error)
        this.loading = false;
      })
    },
    tabChangeHandle() {
      this.getList();
    },
    claim(roundId, $el) {
      $el.setLoading(true);
      this.$api.exerciseOptionFacade(roundId).then(() => {
        $el.setLoading(false);
        this.$api.getBalance();
        this.deleteRow(roundId);
      }).catch(() => {
        $el.setLoading(false);
      })
    },
    exerciseOptionFacade(optionId, btmEl) {
      btmEl.setLoading(true)
      this.$api.exerciseOptionFacade(optionId).then(() => {
        this.$toast.success(this.language.aoption_success)
        this.getList();
      }).catch((err) => {
        console.log(err)
        if (err?.code !== 4001) {
          this.$toast.error(this.language.aoption_fail)
        }
      }).finally(() => {
        btmEl.setLoading(false)
      })
    },
    deleteRow(epoch) {
      let data = this.data;
      let idx = data.findIndex(({round}) => round.epoch === epoch);
      idx !== -1 && data.splice(idx, 1);
    },
    openBlock(hash) {
      window.open(blockConfig.host + hash);
    },
  },
  beforeMount() {
    this.getList();
    this.getCoinPrice(coinsTabs[0].priceContractName, coinsTabs[0].symbol)
    // this.getCoinPrice(coinsTabs[1].priceContractName, coinsTabs[1].symbol)
  }
}
</script>

