import { encodeDefxCallName, BIG_ZERO, OptionType, OptionCoin } from '@/common/util'

const oinitOptionValue = {
  lockupPeriodForHedgedTranches: BIG_ZERO, // 对冲锁定时间
  lockupPeriodForUnhedgedTranches: BIG_ZERO, // 非对冲锁定时间
  totalBalance: BIG_ZERO, // 总质押流动性 = 对冲总质押流动性 + 非对冲总质押流动性
  hedgedShare: BIG_ZERO, // 对冲算力 = 非对冲算力 + 对冲总质押流动性
  unhedgedShare: BIG_ZERO, // 非对冲算力
  hedgedBalance: BIG_ZERO, // 对冲总质押流动性
  unhedgedBalance: BIG_ZERO, // 非对冲总质押流动性
  INITIAL_RATE: BIG_ZERO, // 初始对冲比例
  maxUtilizationRate: BIG_ZERO, // 最大的我不知道的值
  lockedAmount: BIG_ZERO, // 锁定数量
  collateralizationRatio: BIG_ZERO, /// 什么什么占比
  allowance: BIG_ZERO,
  loaded: false
}

export default {
  namespaced: true,
  state: {
    options: {
      up_CAKE: {
        ...oinitOptionValue,
        key: encodeDefxCallName(OptionType.UP, OptionCoin.CAKE),
        coin: OptionCoin.CAKE,
        type: OptionType.UP,
      },
      down_CAKE: {
        ...oinitOptionValue,
        key: encodeDefxCallName(OptionType.DOWN, OptionCoin.CAKE),
        coin: OptionCoin.CAKE,
        type: OptionType.DOWN,
      },
      up_ETH: {
        ...oinitOptionValue,
        key: encodeDefxCallName(OptionType.UP, OptionCoin.ETH),
        coin: OptionCoin.ETH,
        type: OptionType.UP,
      },
      down_ETH: {
        ...oinitOptionValue,
        key: encodeDefxCallName(OptionType.DOWN, OptionCoin.ETH),
        coin: OptionCoin.ETH,
        type: OptionType.DOWN,
      },
    },
    dataInfo: {
      allowance: BIG_ZERO,
      loaded: false
    }
  },
  mutations: {
    setOption(state, {key, info}) {
      let s_info = state.options[key] || {};
      state.options[key] = {
        ...s_info,
        ...info,
        loaded: true,
      };
    },
    setDataInfo(state, data) {
      state.dataInfo = {
        ...state.dataInfo,
        ...data
      };
    },
  },
  actions: {
    setOption({commit}, params) {
      commit('setOption', params);
    },
    setDataInfo({commit}, params) {
      commit('setDataInfo', params);
    },
  },
}