  async getWETHPricePair() {
    return Fetcher.fetchPairData(WETH[BUSD.chainId], BUSD, provider).catch(() => {
      return Fetcher.fetchPairData(WETH[BUSD.chainId], USDT, provider);
    })
  },


  //获取Token价格，如果没有配置通过uniswap查询价格的地址就从oracle预言机查询价格
  async getTokenPrice(address, decimals) {
    let token = new Token(BUSD.chainId, address, decimals);
    let ethPricePair = await this.getWETHPricePair();
    if (WETH[BUSD.chainId].address === address || address === '0xae13d989daC2f0dEbFf460aC112a837C89BAa7cd') {
      let route = new Route([ethPricePair], WETH[BUSD.chainId]);
      return route.midPrice.toSignificant(6);
    } else {
      let WETHTokenPair = await Fetcher.fetchPairData(WETH[BUSD.chainId], token, provider);
      let route = new Route([WETHTokenPair, ethPricePair], token);
      return route.midPrice.toSignificant(6);
    }
  
  },
  async getPriceByOracle(symbol) {
    let item1 = USD_LIST.find(item => item === symbol);
    if(item1) {
      return 1;
    }
    if (symbol == "ETH") {
      const {answer, decimals} = await this.getLatestPrice('AggregatorV3InterfaceEth');
      return  answer/ (10 ** decimals)
    }
    if (symbol == "BNB") {
      const  {answer, decimals} = await this.getLatestPrice('AggregatorV3InterfaceBnb');
      return answer / (10 ** decimals)
    }
  },
  async getPoolMobility(id, pool,contractName) {
    
    let contract = this.dataWeb3.getContract(contractName);
    let token0symbol = pool.token0symbol;
    let token1symbol = pool.token0symbol;
    let item0 = USD_LIST.find(item => item === token0symbol);
    if (item0 != null) {
      let priceInfo = await contract.methods['getPidPrice'](id).call();
      let s0 = priceInfo.tokenAmountMc / Math.pow(10, pool.token0decimals);
      return s0 * 2;
    }
    let item1 = USD_LIST.find(item => item === token1symbol);
    if (item1 != null) {
      let priceInfo = await contract.methods['getPidPrice'](id).call();
      let s1 = priceInfo.quoteTokenAmountMc / Math.pow(10, pool.token1decimals);
      return s1 * 2;
    }
    
    try {
      let token1Price = await this.getTokenPrice(pool.token1, pool.token1decimals);
      let priceInfo = await contract.methods['getPidPrice'](id).call();
      let lpTotalInQuoteToken = priceInfo.quoteTokenAmountMc  * 2;
      let mobility = token1Price * lpTotalInQuoteToken / Math.pow(10, pool.token1decimals);
      return mobility;
    } catch(e) {
      let token0Price = await this.getTokenPrice(pool.token0, pool.token0decimals);
      let priceInfo = await contract.methods['getPidPrice'](id).call();
      let lpTotalInQuoteToken = priceInfo.tokenAmountMc  * 2;
      let mobility = token0Price * lpTotalInQuoteToken / Math.pow(10, pool.token0decimals);
      return mobility;
    }
    
    // console.debug(id, '===================')
    // return 'undefined';
  },
  async getProfitRate(pid, pool,contractName) {
    let {token1decimals,  allocPoint} = pool;
    let averageBlockTime = getBlockDeltaTime();
    let contract = this.dataWeb3.getContract(contractName);
    let p1 = contract.methods['detTokenPerBlock']().call();
    let p2 = contract.methods['totalAllocPoint']().call();
    //自己发行的代币可能在市场没有价格，所以定为初始发行价格
    
    let address;
    if(contractName == "LPPool"){
        address = this.actionWeb3.getAddress("ERC20", "DFT");
    }else{
       address = this.actionWeb3.getAddress("ERC20", "DCoin");
    }
    
    let defxPrice = 0;
    try {
      defxPrice = await this.getTokenPrice(address, 18);
    } catch(e) {
      console.log(e)
      defxPrice = 0.1;
    }
    // let p3 = Promise.resolve(1);
    let [rewardPerBlock, totalAllocPoint] = await Promise.all([p1, p2]);
    //每个块产生的代币
    let rewardPer = rewardPerBlock * allocPoint / (totalAllocPoint * Math.pow(10, token1decimals));
    //年收益
    let annual_profit = rewardPer * 24 * (3600 / averageBlockTime) * 365 * defxPrice * 100;
    //流动性【最优】lpToken的总价值(USDT)
    let mobility = await this.getPoolMobility(pid, pool,contractName);
    //年化率
    let annual_rate = annual_profit / mobility;
    return {
      mobility,
      annual_rate
    }
  },

