      </ul>
    </div>


    <!--    切换网络滑层-->
    <div  class="net switch-box shadow-active animate-inout " :class="{show:showBox&&maskType===1}">
      <!-- <header class="flex jc-between ai-center"> -->
        <!-- <div class="tt text flex ai-center">
          <img class="icon-m" :src="links[walletType].src" alt="">
          {{ links[walletType].name }}
        </div> -->
        <img class="closeStatus" @click="showBox=false" src="../../images/close@2x.png" alt=""/>
      <!-- </header> -->
    <div class="userinfo">
          <div class="userName">
              <div class="userImg">
                 <img src="../../images/home/star.png" alt="" v-if="headPortraitUrlId == '0'">
                 <img :src="urlImg" alt="" v-else>
              </div>

