<script>

import img_fox from '@/images/fox-logo@3x.png'
import img_im from '@/images/imToken-logo@3x.png'
// import img_tp from '@/images/tp-logo@3x.png'
// import img_wall from '@/images/walletconnect-logo@3x.png'
import {mapState, mapActions, mapGetters} from 'vuex';
import {addressFormat, balanceFormatter} from "@/common/dataConvertor";
import {coinMap} from "@/common/walletConfig";
import MButton from "@/components/mButton";

import loginLuck from "./luckDraw/loginLuckDraw"
import dailyLogin from "./luckDraw/dailyLogin"
import editData from "./luckDraw/editData"
import {BN} from "@/common/util";
export default {
  name: 'authorBar',
  components: {MButton,loginLuck,dailyLogin,editData},
  data() {
    return {
      feeRatio: new BN(0),
      available:"",//可领取
      unclaimedData:"",// 待领取
      urlImg:"",
      headPortraitUrlId:"",//获取头像id
      dailyLoginStatus:false,
      invitationMask:false,
      walletType: 0,
      showBox: false,
      maskType: 1,
      links: [
        {type: 0, name: 'MetaMask', src: img_fox},
        {type: 1, name: 'imToken', src: img_im},
        // {type: 2, name: 'Token Pocket', src: img_tp},
        // {type: 3, name: 'WalletConnect', src: img_wall},
      ],
      netList: [
        {type: 1, name: 'BSC'},
        {type: 2, name: 'Polygon', disabled: true},
        {type: 3, name: 'Arbitrum', disabled: true},
      ],
      balanceList: ['BNB', 'USDT', 'DFT','DCoin'],
      timer: null,
      coinMap,
      statusChang:false,
      nickname:"",
      onReceiveAmount:"",
    }
  },
  computed: {
    ...mapState('app', ['address', 'balance']),
    ...mapGetters(['language']),
  },
  watch: {
    '$route.path': function () {
      this.openRecord = false;
    },
    address(address) {
      address && this.getBalance();
    },
   
  },
  methods: {
    balanceFormatter,
    addressFormat,
    ...mapActions('app', ['setUserInfo']),
    togMask() {
      this.maskType = this.address ? 1 : 0;
      this.showBox = !this.showBox;
    },
    // 更改昵称
    changIn(){
      this.statusChang = true;
    },
    closeEdit(){
      this.statusChang = false;
      this.userInfo();
    },
    // 待领取
    unclaimedFn(){
         this.$api.bonusAmountsFn().then((res)=>{
             this.unclaimedData = res 
             this.betAmounts(res);
         })
    },
    // 领取
    receiveFn(){
      let data = this.onReceiveAmount.toString(10)
      this.$api.withdrawBonusFn(data).then(()=>{
          this.unclaimedFn();
      })
    },
    // 最大可领取
    betAmounts(data){
        this.$api.betAmountsFn().then((res)=>{
          let num =  data * 10
          if(res >= num){
              this.available = 0 
              this.onReceiveAmount = data;
          }else{ 
              this.available = num - res
               this.onReceiveAmount = res/ 10;
          }
      })
    },
    login(type) {
      if (this.currentType === type) return;
      switch (type) {
        case 0:
          this.$api.login().then(() => this.walletType = 0);
          break;
        case 1:
          this.walletType = 1;
          break;
        case 2:
          this.walletType = 2;
          break;
        case 3:
          this.walletType = 3;
          break;
      }
      this.showBox = false;
    },
    getBalance() {
      this.$api.getBalance();
      clearInterval(this.timer);
      this.timer = setInterval(() => {
        this.$api.getBalance();
      }, 10000)
    },
    logout() {
      this.setUserInfo({address: '', balance: 0, defx: 0});
      this.showBox = false;
    },
    // 登录抽奖
    loginLotteryFn(){
         this.invitationMask = true;
    },
    noLoginLotteryFn(){
          this.invitationMask = false;
    },
    // 每日签到
    everySignIn(){
        this.dailyLoginStatus = true; 
        this.userInfo(); 
    },
    closeDailyFn(){
      this.dailyLoginStatus = false;  
    },
    
    //  获取头像和昵称
     userInfo(){
       this.$api.getUserInfo1().then((res) => {
          let {nftId,nickname}=res;
          this.headPortraitUrlId = nftId; // 获取头像id
          this.nickname = nickname;// 获取昵称
          this.getNftImgFn(nftId)
       });  
      },
      // 获取头像
      getNftImgFn(headPortraitUrlId){
             this.$api.getNftPic(headPortraitUrlId).then((res)=>{
               this.urlImg =res && res.defxToken &&  res.defxToken.resUrl?res.defxToken.resUrl:'' ;
           })
      },

  },
  async mounted() {
    await this.$api.login();
    await this.userInfo();
    await this.unclaimedFn();
  },
  beforeUnmount(){
      clearInterval(this.timer);// 清除定时器
      this.timer = null;
   },
};
</script>

