export const getDefxCallType = (direction) => {
  if (direction === 0) return OptionType.DOWN
  return OptionType.UP
}

/**
 * 编码 defx的 合约名
 * @param {'up'|'down'} type  方向
 * @param {String} coin  币种
 * @returns {string}
 */
export const encodeDefxCallName = (type, coin) => `${type}_${coin}`

/**
 * 解码 defx的 合约名
 * @param {String} name  合约名
 * @returns {string[]} [方向, 币种]
 */
export const decodeDefxCallName = (name) => name?.split('_')
