<template>
  <div class="dialog" v-show="statusChang">
    <div class="dialog-container">
      <div class="dialog-title">{{language.editData}}</div>
      <div class="reward">* {{language.nickname}}</div>
      <div class="close">
        <img src="../../../images/close@2x.png" alt="" @click="closeFn()" />
      </div>

      
      <div class="nicknames">
        <input v-model="modifiedValue" :placeholder="language.fillNicknames" type="text"  maxlength="12"   onkeyup="this.value=this.value.replace(/[^\w_]/g,'');" />
      </div>

      <div class="reward headPortrait">* {{language.nftAvatar}} </div>
      <div class="upload" @click="uploadAvatar()">
          <img  src="../../../images/luck/jia.png" alt="" v-if="url==''" class="img1"/>
          <img :src="url" alt="" v-else class="img2"/>
      </div>
      <div class="img-title">{{language.selectAdd}}</div>
      <div class="preservation" @click="preservation">
        <m-button  v-if="isApproved" class="hdr-el-blue" :height="36" style="width: 148px" >
           {{language.preservation}}
        </m-button>
         <!-- 授权 -->
        <m-button v-else :loading="loading" @click="setApproved" class="hdr-el-blue" :height="36" style="width: 148px" >
            <span>{{ language.btn_authorize }}</span>
        </m-button>
      </div>
    </div>

     <div v-if="loading"  class="mask flex jc-center ai-center ta-center phone">
        <span><i class="icon-loading"></i><br>{{ language.tip_loading }}...</span>
      </div>
    <choiceNft 
    ref="choinRef"
    :choiceStatusNft="choiceStatusNft"
     @closeDailyNft="closeDailyNft"
     typeStatus="head"
     ></choiceNft>

  </div>
</template>

<script>
import MButton from "@/components/mButton";
import choiceNft from "./../luckDraw/choiceNft";
import { mapGetters,mapState} from 'vuex'; //mapState,
export default {
  components: { MButton ,choiceNft},
  props: {
    statusChang: {
      type: Boolean,
      default: false,
    },
    nickname:{
        type: String,
        default: "",
    },
    urlImg:{
       type: String,
        default: "",
    },
  },
  computed: {
     ...mapState('app', ['address']),
     ...mapGetters(['language']),
  },
  data() {
    return {
      modifiedValue:"",
      choiceStatusNft:false,
      nftId:"",
      url:"",
      isApproved:false,//是否授权
      loading:false,
    };
  },
  watch:{
    statusChang(){
           this.checkIsApproved();
    },
    urlImg(){
      this.url =this.urlImg
    },
    nickname(){
      this.modifiedValue = this.nickname
    }
     
  },
  mounted() {
    this.checkIsApproved();
  },
  methods: {
    // 判断是否授权
    checkIsApproved() {
      if (!this.address) return;
      this.$api.checkIsApproved().then(res => this.isApproved = res)
    },
    // 授权
    setApproved() {
      this.loading = true;
      this.$api.setApprovalForAll().then(() => {
        this.loading = false;
        this.isApproved = true;
      }).catch(() => {
        this.loading = false;
      })
    },

    //  保存
     preservation(){
            this.loading = true;
            this.$api.setUserInfo(this.nftId,this.modifiedValue).then(()=>{
            this.loading = false;
            this.choiceStatusNft = false;
            this.$emit("closeEdit");
          }).catch(() => 
            this.loading = false
          );
     },

    closeFn() {
      this.$emit("closeEdit");
    },
    // 选择头像弹出框 
    uploadAvatar(){ 
        this.choiceStatusNft = true;
        this.$nextTick(()=>{
           this.$refs.choinRef.initQuery();
        })
    },
    //  关闭弹出框
    closeDailyNft(id,url){
         if(id && url){
            this.nftId = id;
            this.url = url;
         }
         this.choiceStatusNft = false;
    },
  },
};
</script>
