<template>
  <div class="dialog" v-show="invitationMask">
    <div class="dialog-container">
      <div class="dialog-title">{{ language.loginLottery }}</div>
      <div class="reward">{{ language.higherTreward }}</div>
      <div></div>
      <div class="close">
        <img src="../../../images/close@2x.png" alt="" @click="closeMask()" />
      </div>

      <div class="luckBox">
        <div class="round_box"></div>

        <div class="img_rotate" ref="rotImg">
          <div class="mydiv">
           
            <div
              :class="`div${index + 1}`"
              v-for="(item, index) in hitAre"
              :key="index"
            >
              {{ item }}
            </div>
          </div>
        </div>


        <div class="center">
          <div class="pointer">
            <span v-if="winNumberData == 0" @click="toDraw">{{ language.clickLuckyDraw }}</span>
            <span v-else style="cursor: no-drop;color:#cccccc">{{ language.clickLuckyDraw }}</span>
          </div>
        </div>
      </div>

      <div class="congratulations">
        <div>{{ language.congratulationsGetting }} !</div>
         <div v-if="winNumberData">{{ winNumberData }} DCoin</div>
         <div v-else>{{ winData }} DCoin</div>
      </div>
    </div>
  </div>
</template>

<script>
import { BN } from "@/common/util";
import { mapGetters } from "vuex"; //mapState,
export default {
  props: {
    invitationMask: {
      type: Boolean,
      default: false,
    },
    headPortraitUrlId: {
      type: String,
      default: "",
    },
  },
  computed: {
    ...mapGetters(["language"]),
  },
  data() {
    return {
      rotate: 0,
      resetRotate: 0,
      hitId: 1, // 1-10
      drawStatus: false,
      valueLuck: "",
      hitAre: [],
      headid: "",
      winNumberData: "",
      winData: "",
      status: "",
     
    };
  },
  watch: {},
  async mounted() {
    let that = this;
    await this.$api.login();
    this.$nextTick();
    let evenTransition = this.whichTransitionEvent();
    let img = this.$refs.rotImg;
    const data = ["2", "1", "6", "5", "4", "3"];
    // 监听 动画结束
    img.addEventListener(evenTransition, tranHand, false);
    function tranHand() {
      // 复位
      that.resetRotate = that.rotate > 360 ? that.rotate % 360 : 0;
      img.style.transition = "none 0s ease 0s";
      img.style.transform = `rotate(${that.resetRotate}deg)`;
      that.valueLuck = data[that.hitId - 1];
      that.winData = that.winNumberData;
      // alert( this.winNumberData,"54444444444444" )
      // console.log(this.hitAre.map().indexOf(this.winNumberData));
      //alert(`抽奖结果【 ${hitAre[that.hitId - 1]} 】`);
      that.drawStatus = false;
    }
    this.userInfo();
    this.getInit();
  },
  methods: {
    //  获取头像和昵称
    userInfo() {
      this.$api
        .getUserInfo1()
        .then(({ nftId }) => {
          return nftId;
        })
        .then((nftId) => {
          return this.$api.getQuality(nftId);
        })
        .then(({ defxToken }) => {
          if(defxToken){
            this.getWinNumber(defxToken.quality);
          }
        });
    },
    getWinNumber(quality) {
      let size = 6;
      let num = 10 + quality * 5;
      let arr = [];
      for (var i = 0; i < size; i++) {
        arr.push(num + i);
      }
      this.hitAre = arr;
    },
    closeMask() {
      this.$emit("noLoginLotteryFn");
    },
    start() {
      this.$refs.rotImg.style.transition = "all 3s ease 0s";
      this.$refs.rotImg.style.transform = `rotate(${this.rotate}deg)`;
    },
    start1() {
      this.$refs.rotImg.style.transition = "all 60s ease 0s";
      this.$refs.rotImg.style.transform = `rotate(10800deg)`;
    },

    getInit(){
          let day = parseInt(new Date().getTime() / 1000 / 3600 / 24);
          this.$api.lotterys(day).then((res) => {
          this.winNumberData =  parseInt(  new BN(res).toDisplay("balance").myFixed() );
        })
    },

    toDraw() {
      if(this.winNumberData !='' || this.winNumberData !=0){
        return false
      }
      this.start1();
      if (this.drawStatus) {
        console.log("正在抽奖中");
        return;
      }
      this.$api.userLottery() .then(() => {
          return 1;
        }).then(() => {
          this.$refs.rotImg.style.transition = "all 0s ease 0s";
          let day = parseInt(new Date().getTime() / 1000 / 3600 / 24);
          this.$api.lotterys(day).then((res) => {
            this.winNumberData = parseInt(
              new BN(res).toDisplay("balance").myFixed()
            );
            /**
             * 圆盘共 6 份 每份 60度, 停位置(id)度数 (id - 1)*36
             * 基数 6圈 360*6
             * this.rotate 当前角度
             * **/
            let reset = 360 * 4;
            let idx = this.arrIndex(this.hitAre, this.winNumberData) + 1;
            console.info(`id = ${idx}`);
            // 设置命中
            this.hitId = idx;

            // 需要多转角度
            let addRotate = this.resetRotate > 0 ? 360 - this.resetRotate : 0;
            // 总共角度
            let allRotate = this.rotate + (idx - 1) * 60 + reset + addRotate;
            // 角度限制
            this.rotate = allRotate;
            this.start();
          });
        })
        .catch(() => {
          this.drawStatus = false;
          this.$refs.rotImg.style.transform = `rotate(0deg)`;
          this.$refs.rotImg.style.transition = "all 0s ease 0s";
        });
      // 标记状态
      this.drawStatus = true;
      //this.start1()
    },
    arrIndex(arr, value) {
      let start = 1;
      let index = 0;
      for (let i = start; i >= 0; i--) {
        if (value == arr[i]) {
          return index;
        }
        index++;
      }
      let j = 3;
      for (let i = j; j < arr.length; i++) {
        if (value == arr[i]) {
          return index;
        }
        index++;
      }
      return 2;
    },
    // 递归计算角度 不超过 360*6
    setRotate(deg) {
      let rest = deg - 360;
      return rest > 360 * 6 ? this.setRotate(rest) : deg;
    },
    getRandomInt(min, max) {
      // 向上收
      min = Math.ceil(min);
      // 向下收
      max = Math.floor(max);
      return Math.floor(Math.random() * (max - min)) + min; //不含最大值，含最小值
    },
    // 动画兼容
    whichTransitionEvent() {
      let el = document.createElement("span"),
        transitions = {
          transition: "transitionend",
          OTransition: "oTransitionEnd",
          MozTransition: "transitionend",
          WebkitTransition: "webkitTransitionEnd",
        };
      for (let t in transitions) {
        if (el.style[t] !== undefined) {
          return transitions[t];
        }
      }
      el = null;
    },
  },
};
</script>
